.timi-loader, .timi-error {
  position: fixed;
  left: 0;
  top: 0;
  width: 100vw;
  height: 100vh;
  background:#fff url('./resources/images/vectors/loader.svg') no-repeat center center / 4%;
  z-index: 99999;
}

html {
  overflow: hidden;
}

body {
    position: relative;
    margin: 0;
    padding: 0;
    font-family: 'Roboto', sans-serif;
    font-weight: 400;
    color: #000;
    height: 100vh;
    font-size: 1.4vw;
    letter-spacing: 0.4pt;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
  }
  
  input, textarea, select, button {
    font-size: 200%;
    font-family: 'Roboto', sans-serif;
  }

  textarea:focus, input:focus, select:focus {
    outline: none;
  }
  
  .timi-ui-cell-slave {
    display: inline-block;
  }
  
  .timi-map {
    position: fixed;
    top: 0;
    left: 61.63vw;
    right: 0;
    bottom: 0;
  }
  
  .timi-ex {
    position: absolute;
    top: 0;
    left: 0;
    right: 37.51vw;
    bottom: 0;
    background-color: #ffffff;
  }
  
  .timi-ex .timi-logical-hub {
    position: relative;
    width: 100%;
    height: 100%;
  }
  
  .timi-ex .timi-ex-filter {
    position: fixed;
    left: 0;
    top: 0;
    width: inherit;
    background-color: #ffffff;
    -webkit-box-shadow: 0 16px 16px -5px #00000012;
            box-shadow: 0 16px 16px -5px #00000012;
    white-space: nowrap;
    z-index: 1;
  }
  
  .timi-ex .timi-ex-filter .timi-input {
    display: inline-block;
    padding: 30px;
    padding-right: 0;
    width: 50%;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    border: 2pt solid #EDF0F4;
    border-left: 0px;
    border-radius: 0 25px 25px 0;
    font-size: 200%;
    -webkit-transition: 0.3s color ease-in-out;
    -o-transition: 0.3s color ease-in-out;
    transition: 0.3s color ease-in-out;
  }
  
  .timi-ex .timi-ex-filter .timi-ex-data {
    padding: 30px 30px 0 30px;
    
  }
  
  .timi-ex .timi-ex-filter .timi-drop-call {
    display: inline-block;
    padding: 30px 0;
    padding-left: 2vw;
    width: 20%;
    border: 2pt solid #EDF0F4;
    border-radius: 25px;
    cursor: pointer;
    text-transform: uppercase;
    background: url("./resources/images/vectors/icons/arrow-down.svg") no-repeat center right 30px/30px;
    -webkit-appearance: none;
    -moz-appearance: none;
         appearance: none;
    -webkit-transition: 0.3s border-color ease-in-out;
    -o-transition: 0.3s border-color ease-in-out;
    transition: 0.3s border-color ease-in-out;
  }

  .timi-ex .timi-ex-filter .timi-drop-call::-ms-expand {
    display: none;
  }
  
  .timi-ex .timi-ex-filter .timi-drop-call:first-child {
    border-radius: 25px 0 0 25px;
  }
  
  .timi-ex .timi-ex-filter .timi-drop-switch {
    display: inline-block;
    padding: 30px 0;
    width: 10%;
    font-size: 200%;
    background: url("./resources/images/vectors/icons/exchange.svg") no-repeat center center / 40px;
  }
  
  .timi-ex .timi-ex-filter .timi-ex-sort {
    padding: 30px;
    padding-right: 37px;
    font-size: 128%;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
  }
  
  .timi-ex .timi-ex-filter .timi-ex-sorter {
    color: #008AEE;
  }
  
  .timi-ex .timi-ex-filter .timi-ex-sorter-active .timi-ex-sorter-icon {
    position: relative;
  }
  
  .timi-ex .timi-ex-filter .timi-ex-sorter-active .timi-ex-sorter-icon:after {
    display: block;
    position: absolute;
    top: 18px;
    left: 100%;
    width: 20px;
    height: 13px;
    margin-left: 10px;
    content: " ";
    background-color: #008aee;
    -webkit-mask-repeat: no-repeat;
    mask-repeat: no-repeat;
  }
  
  .timi-ex .timi-ex-filter .timi-ex-sorter-active .timi-ex-sorter-icon:after {
    -webkit-mask-image: url("./resources/images/vectors/icons/arrow-up.svg");
    mask-image: url("./resources/images/vectors/icons/arrow-up.svg");
  }
  
  .timi-ex .timi-ex-filter .timi-ex-sorter-DESC .timi-ex-sorter-icon:after {
    -webkit-mask-image: url("./resources/images/vectors/icons/arrow-down.svg");
    mask-image: url("./resources/images/vectors/icons/arrow-down.svg");
  }

  .timi-ex .timi-ex-sort .timi-ex-fresh {
    margin-left: 4%;
  }
  
  .timi-ex .timi-ex-list {
    padding: 0;
    width: 62.5vw;
    background-color: #ffffff;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    overflow-x: hidden;
    overflow-y: auto;
  }

  .ReactVirtualized__List {
    outline: 0;
  }
  
  .timi-ex .timi-ex-bank {
    position: relative;
    padding: 2vw 30px;
    border: 3px solid transparent;
    border-left: 0;
    border-right: 0;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    -webkit-transition: 0.4s all ease;
    -o-transition: 0.4s all ease;
    transition: 0.4s all ease;
    -webkit-box-shadow: 0 -1px 0px 0 #3a8afd26;
            box-shadow: 0 -1px 0px 0 #3a8afd26;
  }
  /* .timi-ex .timi-ex-bank::after {
    position: absolute;
    display: block;
    content: " ";
    top: calc(100% - 1px);
    left: 0;
    width: 100%;
    height: 1px;
    background-color: #e2e2e2;
  } */

  .timi-ex .timi-ex-bank.timi-list-item-selected {
    -webkit-box-shadow: 0 16px 16px -5px #00000012;
            box-shadow: 0 16px 16px -5px #00000012;
    border: 3px solid #008AEE;
    border-left: 0;
    border-right: 0;
  }
  
  /* .timi-ex .timi-ex-bank:last-child {
    border-bottom: 0;
  } */
  
  .timi-ex .timi-ex-bank .timi-ui-cell-slave div {
    line-height: 40px;
    font-size: 80%;
  }
  
  .timi-ex .timi-ex-bank .timi-ex-name {
    padding-left: 163px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    /* background: no-repeat left top 8px / 42px; */
    background-repeat: no-repeat;
    background-position: left center;
    -webkit-transition: 0.4s padding;
    -o-transition: 0.4s padding;
    transition: 0.4s padding;
  }
  
  /* .timi-ex .timi-ex-bank .timi-ex-name.timi-ex-fav-to {
    background-image: url("./resources/images/vectors/icons/fav-to.svg");
  } */
  
  .timi-ex .timi-ex-bank .timi-ex-name.timi-ex-fav-has {
    background-image: url("./resources/images/vectors/icons/fav-has.svg");
  }
  
  .timi-ex .timi-ex-bank .timi-ex-street {
    position: relative;
    color: #008AEE;
    overflow: hidden;
  }

  .timi-ex .timi-ex-bank .timi-ex-street::after {
    position: absolute;
    content: " ";
    right: 0;
    top: 0;
    bottom: 0;
    width: 46%;
    background-image: -webkit-gradient(linear, left top, right top, from(rgba(255,255,255,0)), to(rgba(255,255,255,1)));
    background-image: -webkit-linear-gradient(left, rgba(255,255,255,0) 0%, rgba(255,255,255,1) 100%);
    background-image: -o-linear-gradient(left, rgba(255,255,255,0) 0%, rgba(255,255,255,1) 100%);
    background-image: linear-gradient(to right, rgba(255,255,255,0) 0%, rgba(255,255,255,1) 100%);
  }
  
  .timi-ex .timi-ex-bank .timi-delta-plus {
    color: #a1aeb7;
  }
  
  .timi-ex .timi-ex-bank .timi-delta-minus {
    color: #F23D49;
  }

  .timi-ex-caaaar {
    padding-right: 35px;
    /* background: url("./resources/images/vectors/icons/caaaar.svg") no-repeat right center / 26px; */
  }

  .timi-ex .timi-ex-bank .timi-ex-fresh {
    margin-left: 4%;
  }
  
  .timi-ex .timi-ex-bank .timi-ex-fresh-date {
    color: #ABABAB;
  }
  
  .timi-ex .timi-ui-cell-master.timi-ex-sort .timi-ex-name, .timi-ex .timi-ui-cell-master.timi-ex-bank .timi-ex-name {
    width: 50%;
    white-space: nowrap;
  }
  .timi-ex .timi-ui-cell-master.timi-ex-bank .timi-ex-name-fader {
    position: relative;
    display: inline-block;
    overflow: hidden;
    width: 100%;
    margin-bottom: -6px;
  }
  .timi-ex .timi-ui-cell-master.timi-ex-bank .timi-ex-name-fader::after {
    position: absolute;
    content: " ";
    right: 0;
    top: 0;
    bottom: 0;
    width: 46%;
    background-image: -webkit-gradient(linear, left top, right top, from(rgba(255,255,255,0)), to(rgba(255,255,255,1)));
    background-image: -webkit-linear-gradient(left, rgba(255,255,255,0) 0%, rgba(255,255,255,1) 100%);
    background-image: -o-linear-gradient(left, rgba(255,255,255,0) 0%, rgba(255,255,255,1) 100%);
    background-image: linear-gradient(to right, rgba(255,255,255,0) 0%, rgba(255,255,255,1) 100%);
  }
  
  .timi-ex .timi-ui-cell-master.timi-ex-sort .timi-ex-buy, .timi-ex .timi-ui-cell-master.timi-ex-bank .timi-ex-buy {
    width: 20%;
    vertical-align: top;
  }
  
  .timi-ex .timi-ui-cell-master.timi-ex-sort .timi-ex-sell, .timi-ex .timi-ui-cell-master.timi-ex-bank .timi-ex-sell {
    width: 30%;
    vertical-align: top;
  }
  


